import Link from 'next/link'
import React, { useState } from "react";
import { Grid, Image, Divider, Button, Header, Modal, Input, Form, Label } from 'semantic-ui-react'
import axios from "axios"

function updateUserSettings(props) { {
    const[name, setName] = useState("");
    const[email, setEmail] = useState("");
    const[sms, setSMS] = useState("");
    const[address, setAddress] = useState("");

    const handleSubmit = (evt) => {
        evt.preventDefault();
        var formData = new FormData();
        formData.append("Name", name);
        formData.append("Email", email);
        formData.append("SMS", sms);
        formData.append("Address", address);
        console.log(formData);
    }

    const onChangeName = e =>{
      setName(e.target.value);
    }

    const onChangeEmail = e =>{
      setEmail(e.target.value);
    }

    const onChangeSMS = e =>{
      setSMS(e.target.value);
    }

    const onChangeAddress = e =>{
      setAddress(e.target.value);
    }

    return (
        <div style={{ paddingLeft: '8px' }}>
          <div class=" ui clearing segment">
            <link rel="stylesheet" href="//cdn.jsdelivr.net/npm/semantic-ui@2.4.2/dist/semantic.min.css" />
            <h1 class="ui header">WelcomeIN</h1>
            <div class="ui inverted menu">
            <Link href="/index">
                <a className="item">
                  Home
              </a>
              </Link>
              <Link href="/users">
                <a className="item">
                  Authorized Individuals
              </a>
              </Link>
              <Link href="/footage">
                <a className="item">
                  Footage</a>
              </Link>
              <Link href="/settings">
                <a className="active item">
                   Settings
              </a>
              </Link>
    
            </div>
            <div style={{ paddingTop: '16px' }} class="ui center aligned grid">
              <h2 class="ui icon header">
                <i class="settings icon"></i>
                <div class="content">
                  Update User Information
        <div class="sub header">Change your account settings and e-mail preferences.</div>
                </div>
              </h2>
            </div>
    
            <div class="ui divider"></div>
            <div className="content">Change Account Settings: </div>
            <div style={{ paddingLeft: '8px', paddingTop: '8px' }}>
    
            <form class="ui form segment" onSubmit={handleSubmit}>
            <div class="ui form segment" style={{paddingRight:'32%', paddingLeft:'40%', paddingBottom: '8px'}} class="field">
                    <label>Name</label>
                    <input value={name} type="text" name="Name" placeholder="Name" onChange={onChangeName}></input>
                </div>
            </form>
            <div class="ui form segment" style={{paddingRight:'32%', paddingLeft:'40%', paddingBottom: '8px'}} class="field">
                    <label>E-mail</label>
                    <input value={email} type="text" name="Name" placeholder="Email" onChange={onChangeEmail}></input>
                </div>
            <form>
            <div class="ui form segment" style={{paddingRight:'32%', paddingLeft:'40%', paddingBottom: '8px'}} class="field">
                    <label>SMS Number</label>
                    <input type="text" value={sms} name="Name" placeholder="Phone Number" onChange={onChangeSMS}></input>
                </div>
            </form>
    
            <form>
            <div style={{paddingRight:'32%', paddingLeft:'40%'}} class="field">
                    <label>Home Address</label>
                    <input value={address} type="text" name="Name" placeholder="Address" onChange={onChangeAddress}></input>
                </div>
            </form>
    
            <Link href="/settings"><button style={{ marginLeft: '90%' }} class="positive ui button">Save</button></Link>
           
            </div>
          </div>
        </div> //ending page
      )

}

 


} export default updateUserSettings
